@use "../styles/vars.scss";

*,
::before,
::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  width: 100%;
  min-height: 100vh;
}

body {
  min-width: 100%;
  min-height: 100vh;
  background-color: vars.$clr-background;
  color: vars.$clr-main-text;
  font-size: vars.$fs-md;
  font-family: vars.$fm-main;
  line-height: 1.3;
  overflow-x: hidden;
}

img {
  display: block;
  max-width: 100%;
}

a {
  cursor: pointer;
  color: inherit;
  text-decoration: none;
}

button {
  outline: none;
  cursor: pointer;
}

input {
  outline: none;
  color: inherit;
}
